import React, {Component} from 'react';
import ApiService from "./ApiService";
import { Button } from "reactstrap";

import '../css/layout.css';

class ChatComp extends Component{
	textRef = React.createRef();

	constructor(props){
		super(props);
			
        this.state={
			isInit : true,
			senderId:"customer",
			email:window.localStorage.getItem("userEmail"),
			message:null,
			location:null,
			replies:[
				{
					className:'left',
					senderId:'admin',
					message:'안녕하세요.\n무엇을 도와드릴까요?',
					isButton:true
				},
				{
					className:'left',
					senderId:'admin',
					message:'1.버그신고 / 2. 업무문의',
					isButton:false
				}
			],
			flag:"normal"
		}
	}
		
	handleKeyPress=(event)=>{
		if(event.charCode === 13){
			this.sendMessage('right', 'customer', this.state.message);
			console.log("snd: " + this.state.message);
			var data={
				type:'null',
				senderId:this.state.senderId,
				message:this.state.message,
				location:window.location.href,
				email:this.state.email
			}
			this.receiveMessage(data);
		}
	}
	
	sendMessage=(lr, sender, msg)=>{
		var data={
			senderId:sender,
			message:msg,
		}
		this.addChatDetail(data, lr);
	}
	
	addChatDetail=(data, lr)=>{
		
		this.setState({replies:[...this.state.replies,{
			uuid:this.state.replies.length+1,
			className:lr,
			senderId:data.senderId,
			message:data.message
		}]})
	}

	receiveMessage=(data)=>{
		ApiService.sendMsg(data)
			.then(res=>{
				console.log(JSON.stringify(res.data));
				this.addChatDetail(res.data,'left');
				this.clearMessage();
			});
	}
	
	clearMessage=()=>{
		this.textRef.current.value = '';
		this.state.message = '';
	}
	
	onChange=(e)=>{
		this.setState({[e.target.name]:e.target.value})
	}
	
	render(){
		return(
            <div className="dimmed_layer_wrapper">
                <div className="full_layer">
                    <div className="common_alert"> 
				        <div className="chatName">
				            <span>문의하기</span>
							<Button className="exitBtn" color="secondary" size="sm" onClick={this.props.onClose}><span>나가기</span></Button>
				        </div>

					    <div class="chat">
					        <ul>{
								this.state.replies.map((e)=>(
									<li class={e.className}>
										<div class="sender">
											<span>{e.senderId}</span>
										</div>
										<div class="message">
											<span>{e.message}</span>
										</div>
										<div>
											{e.isButton ? <button class="btn">ggg</button> : <div></div>}
										</div>
									</li>
								))
							}
					        </ul>
					    </div>
					    <div class="input-div">
					        <textarea
								placeholder="문의사항을 기재해주세요."
								name="message"
								ref={this.textRef}
								onChange={this.onChange}
								rows={3}
								onKeyPress={this.handleKeyPress}/>
					    </div>
	
					    <div class="chat format">
					        <ul>{
								this.state.replies.map((e)=>(
									<li class={e.className}>
										<div class="sender">
											<span>{e.senderId}</span>
										</div>
										<div class="message">
											<span>{e.message}</span>
										</div>
									</li>
								))
							}
					        </ul>
                    	</div>
                	</div>
            	</div>
			</div>
		);
	}
}

export default ChatComp;